import React, {Component} from 'react';
import './site.less'
import {Button, Modal, Table, notification, Form, Input} from "antd";
import SaveForm from "./save-form";
import UpdateForm from "./update-form";
import LinkButton from "../../components/link-button";
import {reqDeleteSite, reqSaveSite, reqSites, reqUpdateSite} from "../../api";


export default class Site extends Component {
    state = {
        loading: false,
        sites: [],
        total: 0,
        siteName: '',
        pageNum: 1,
        pageSize: 10,
        showStatus: 0,/* 0 1 2*/
    };

    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'siteName',
            },
            {
                title: '',
                dataIndex: 'siteMark',
            },
            {
                title: 'PR',
                dataIndex: 'pr',
            },
            {
                title: '',
                dataIndex: 'domain',
            },
            {
                title: '',
                dataIndex: 'backgroundAddress',
            },
            {
                title: '',
                dataIndex: 'realName',
            },
            {
                title: '',
                dataIndex: 'modifyAt',
            },
            {
                title: '',
                dataIndex: 'modifyBy',
            },
            {
                title: '',
                render: (site) => (/****/
                    <span>
                        <LinkButton onClick={() => this.showUpdateModal(site)}></LinkButton>
                        <LinkButton onClick={() => this.deleteSite(site.id)}></LinkButton>
                    </span>
                )
            },
        ];
    };

    deleteSite = (id) => {
        Modal.confirm({
            title: '?',
            okText: '',
            cancelText: '',
            onOk: () => {
                reqDeleteSite(id);
                this.getSites();
            },
        });
    };
    /**/
    getSites = async (pageNum) => {
        this.setState({loading: true});
        const result = await reqSites(pageNum, this.state.pageSize, this.state.siteName);
        const {list, total} = result.data;
        this.setState({total, sites: list, loading: false});
    };
    /**/
    showSaveModal = () => {
        this.setState({
            showStatus: 1
        })
    };
    /**/
    showUpdateModal = (site) => {
        this.site = site;
        this.setState({
            showStatus: 2
        })
    };
    /**/
    handleCancel = () => {
        if (this.updateForm) {
            this.updateForm.resetFields();
        }
        if (this.saveForm) {
            this.saveForm.resetFields();
        }
        this.setState({
            showStatus: 0
        })
    };
    /**/
    saveSite = () => {
        /**/
        this.saveForm.validateFields(async (error, values) => {
            if (!error) {
                this.setState({
                    showStatus: 0
                });
                const result = await reqSaveSite(values);
                this.saveForm.resetFields();/**/
                if (result.code === 0) {
                    notification['success']({
                        message: '',
                    });
                    this.getSites();
                }
            }

        });

    };
    /**/
    updateSite = () => {
        /**/
        this.updateForm.validateFields(async (error, values) => {
            if (!error) {
                this.setState({
                    showStatus: 0
                });
                const {siteName, pr, domain, backgroundAddress} = values;
                if (siteName) {
                    this.site.siteName = siteName;
                }
                if (pr) {
                    this.site.pr = pr;
                }
                if (domain) {
                    this.site.domain = domain;
                }
                if (backgroundAddress) {
                    this.site.backgroundAddress = backgroundAddress;
                }
                this.updateForm.resetFields();/**/
                const result = await reqUpdateSite(this.site);
                if (result.code === 0) {
                    notification['success']({
                        message: '',
                    });
                }
                this.getSites();
            }
        });


    };

    componentWillMount() {
        this.initColumns();
    }

    componentDidMount() {
        this.getSites(1);
    }

    render() {
        const {sites, loading, total, showStatus, siteName, pageNum, pageSize} = this.state;
        const site = this.site;
        return (
            <div style={{padding: '20px 20px 0'}}>
                <div className='content-search'>
                    <Form layout="inline">
                        <Form.Item label=''>
                            <Input value={siteName} onChange={event => this.setState({siteName: event.target.value})}/>
                        </Form.Item>
                        <Form.Item>
                            <Button type='primary' onClick={() => {
                                this.getSites(1);
                                this.setState({pageNum: 1})
                            }}></Button>
                        </Form.Item>
                    </Form>
                </div>
                <div className='add-button'><Button type='primary' onClick={this.showSaveModal}></Button></div>
                <Table rowKey='id' dataSource={sites} columns={this.columns} loading={loading}
                       pagination={{
                           defaultCurrent: 1,
                           current: pageNum,
                           defaultPageSize: pageSize,
                           total: total,
                           onChange: (value) => {
                               this.getSites(value);
                               this.setState({pageNum: value})
                           }
                       }}/>
                <Modal
                    title=""
                    visible={showStatus === 1}
                    okText=''
                    cancelText=''
                    onOk={this.saveSite}
                    onCancel={this.handleCancel}>
                    <SaveForm setForm={(form) => {
                        this.saveForm = form
                    }}/>
                </Modal>
                <Modal
                    title=""
                    visible={showStatus === 2}
                    okText=''
                    cancelText=''
                    onOk={this.updateSite}
                    onCancel={this.handleCancel}>
                    <UpdateForm site={site} setForm={(form) => {
                        this.updateForm = form
                    }}/>
                </Modal>
            </div>

        )
    }
}
