import React, {Component} from 'react';
import {Form, Icon, Input, Button, notification} from 'antd';
import './login.less';
import logo from '../../assets/images/logo.svg';
import {connect} from "react-redux";
import {register} from '../../actions';
import {Redirect} from 'react-router-dom';

class Register extends Component {
    handleSubmit = (event) => {
        event.preventDefault();
        const form = this.props.form;
        form.validateFields((err, values) => {
            if (!err) {
                this.props.register(values);
            } else {
                console.log('', values);
            }
        });
    };

    loginJump = () => {
        this.props.history.push('/login')
    };

    validatorPassword = (rule, value, callback) => {
        const length = value && value.length;
        const pwdReg = /^[a-zA-Z0-9_]+$/;
        if (!value) {
            callback('')
        } else if (length < 6) {
            callback('6')
        } else if (length > 20) {
            callback('20')
        } else if (!pwdReg.test(value)) {
            callback('')
        } else {
            callback();
        }
    }

    compareToFirstPassword = (rule, value, callback) => {
        const {form} = this.props;
        if (value && value !== form.getFieldValue('password')) {
            callback('!');
        } else {
            callback();
        }
    };

    render() {

        const registerUser = this.props.registerUser
        if (registerUser && registerUser.loginName) {
            return <Redirect to='/login'/>
        }
        const {getFieldDecorator} = this.props.form;
        return (
            <div id="register">
                <header>
                    <div className="logo">
                        <a href='/'>
                            <img src={logo} alt="logo"/>
                            <span></span>
                        </a>
                    </div>
                    <div className="desc"></div>
                </header>
                <section>
                    <h2></h2>
                    <Form onSubmit={this.handleSubmit} className="login-form">
                        <Form.Item>
                            {getFieldDecorator("loginName", {
                                rules: [
                                    {request: true, whitespace: true, message: ""},
                                    {min: 4, message: "4"},
                                    {max: 20, message: "20"},
                                    {pattern: /^[a-zA-Z0-9_]+$/, message: ""},
                                ]
                            })(
                                <Input
                                    prefix={<Icon type="user-add" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    placeholder=""
                                />
                            )}
                        </Form.Item>

                        <Form.Item>
                            {getFieldDecorator("realName", {})(
                                <Input
                                    prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    placeholder=""
                                />
                            )}
                        </Form.Item>

                        <Form.Item>
                            {getFieldDecorator("password", {
                                rules: [
                                    {
                                        validator: this.validatorPassword
                                    }
                                ]
                            })(
                                <Input.Password
                                    prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    type="password"
                                    placeholder="6,"
                                />
                            )}
                        </Form.Item>

                        <Form.Item>
                            {getFieldDecorator("password2", {
                                rules: [
                                    {
                                        required: true,
                                        message: '',
                                    },
                                    {
                                        validator: this.compareToFirstPassword,
                                    }
                                ]
                            })(
                                <Input.Password
                                    prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    type="password"
                                    placeholder=""
                                />
                            )}
                        </Form.Item>

                        <Form.Item>
                            {getFieldDecorator("mobilePhone", {
                                rules: [
                                    {required: true, message: '!'}
                                ]
                            })(
                                <Input
                                    prefix={<Icon type="mobile" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    placeholder=""
                                />
                            )}
                        </Form.Item>

                        <Form.Item>
                            {getFieldDecorator("email", {
                                rules: [
                                    {
                                        type: 'email',
                                        message: '!',
                                    },
                                ]
                            })(
                                <Input
                                    prefix={<Icon type="mail" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    placeholder=""
                                />
                            )}
                        </Form.Item>

                        <Form.Item>
                            {getFieldDecorator("companyName", {})(
                                <Input
                                    prefix={<Icon type="home" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    placeholder=""
                                />
                            )}
                        </Form.Item>


                        <Form.Item>
                            <Button type="primary" htmlType="submit" className="register-form-button"
                                    size="large"></Button>
                            <Button type="link" className="register-to-login" onClick={this.loginJump}></Button>
                        </Form.Item>
                    </Form>
                </section>
                <footer>
                    <div className="links">
                        <a></a>
                        <a></a>
                        <a></a>
                    </div>
                    <div className="copyright">
                        Copyright  2019 
                    </div>
                </footer>
            </div>
        )
    }
}

export default connect(
    state => ({registerUser: state.registerUser}),
    {register})(Form.create()(Register))

