import React, {Component} from 'react';
import {Form, Icon, Input, Button, Checkbox} from 'antd';
import {Redirect} from 'react-router-dom';
import './login.less';
import logo from '../../assets/images/logo.svg';
import {connect} from "react-redux";
import {login, resetRegister} from '../../actions';

class Login extends Component {
    handleSubmit = (event) => {
        //
        event.preventDefault();
        //form
        const form = this.props.form;
        form.validateFields((err, values) => {
            if (!err) {
                const {username, password} = values;
                this.props.login(username, password);
            } else {
                console.log('', values);
            }
        });
    };
    registerJump = () => {
        //event.preventDefault();
        this.props.history.push('/register')
    };

    //
    validatorPassword = (rule, value, callback) => {
        const length = value && value.length;
        const pwdReg = /^[a-zA-Z0-9_]+$/;
        if (!value) { // callback 
            callback('')
        } else if (length < 6) {
            callback('6')
        } else if (length > 12) {
            callback('12')
        } else if (!pwdReg.test(value)) {
            callback('')
        } else {
            callback();//  callback
        }
    }

    render() {
        this.props.resetRegister();
        // ,  admin
        const user = this.props.user;
        if (user && user.name) {
            return <Redirect to='/'/>
        }
        //form
        const form = this.props.form;
        const {getFieldDecorator} = form;

        return (
            <div id="login">
                <header>
                    <div className="logo">
                        <a href='/'>
                            <img src={logo} alt="logo"/>
                            <span></span>
                        </a>
                    </div>
                    <div className="desc"></div>
                </header>
                <section>
                    <Form onSubmit={this.handleSubmit} className="login-form">
                        <Form.Item>
                            {getFieldDecorator("username", {//
                                //
                                rules: [
                                    {request: true, whitespace: true, message: ""},
                                    {min: 4, message: "4"},
                                    {max: 20, message: "20"},
                                    {pattern: /^[a-zA-Z0-9_]+$/, message: ""},
                                ]
                            })(
                                <Input
                                    prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    placeholder="admin"
                                />
                            )}
                        </Form.Item>
                        <Form.Item>
                            {getFieldDecorator("password", {
                                rules: [
                                    {
                                        validator: this.validatorPassword
                                    }
                                ]
                            })(
                                <Input
                                    prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                    type="password"
                                    placeholder="123456"
                                />
                            )}
                        </Form.Item>
                        <Form.Item>
                            <Checkbox></Checkbox>
                            <Button type="link" className="login-form-forgot" onClick={this.test}></Button>
                            <Button type="primary" htmlType="submit" className="login-form-button"
                                    size="large"></Button>
                            <Button type="link" onClick={this.registerJump}></Button>
                        </Form.Item>
                    </Form>
                </section>
                <footer>
                    <div className="links">
                        <a></a>
                        <a></a>
                        <a></a>
                    </div>
                    <div className="copyright">
                        Copyright  2019 
                    </div>
                </footer>
            </div>
        )
    }
}

