import React, {Component} from 'react';
import {Form, Input} from 'antd';
import PropTypes from 'prop-types';

const Item = Form.Item;

class UpdateForm extends Component {

    static propTypes = {
        site: PropTypes.object,
        setForm: PropTypes.func,
    };

    componentWillMount() {
        /*formsetForm*/
        this.props.setForm(this.props.form);
    }

    render() {
        const {site} = this.props;
        const form = this.props.form;
        const {getFieldDecorator} = form;
        const formItemLayout = {
            labelCol: {
                xs: {span: 24},
                sm: {span: 4},
            },
            wrapperCol: {
                xs: {span: 24},
                sm: {span: 18},
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };
        return (
            <Form  {...formItemLayout}>
                <Item label="" required="true">
                    {
                        getFieldDecorator("siteName", {
                            initialValue: site.siteName,
                            rules: [
                                {required: true, message: ''}
                            ]
                        })(
                            <Input placeholder=""/>
                        )
                    }
                </Item>
                <Item label="PR">
                    {
                        getFieldDecorator("pr", {
                            initialValue: site.pr
                        })(
                            <Input placeholder="PR"/>
                        )
                    }
                </Item>
                <Item label="">
                    {
                        getFieldDecorator("domain", {
                            initialValue: site.domain
                        })(
                            <Input placeholder=""/>
                        )
                    }
                </Item>
                <Item label="">
                    {
                        getFieldDecorator("backgroundAddress", {
                            initialValue: site.backgroundAddress
                        })(
                            <Input placeholder=""/>
                        )
                    }
                </Item>
            </Form>
        )
    }
}

export default Form.create()(UpdateForm);
