import React, {Component} from 'react';
import LinkButton from "../../components/link-button";
import {Button, Form, Input, Modal, notification, Table} from "antd";
import {reqDeleteUser, reqUsers, reqUpdateUser} from "../../api";
import UpdateForm from "../user/update-form";

export default class User extends Component {
    state = {
        loading: false,
        users: [],
        total: 0,
        userName: '',
        pageNum: 1,
        pageSize: 10,
        showStatus: 0,/* 0 2*/
    };

    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'loginName',
            },
            {
                title: '',
                dataIndex: 'realName',
            },
            {
                title: '',
                dataIndex: 'mobilePhone',
            },
            {
                title: '',
                dataIndex: 'email',
            },
            {
                title: '',
                dataIndex: 'companyName',
            },
            {
                title: '',
                dataIndex: 'roleName',
            },
            {
                title: '',
                dataIndex: 'description',
            },
            {
                title: '',
                dataIndex: 'createdBy',
            },
            {
                title: '',
                dataIndex: 'createdAt',
            },
            {
                title: '',
                render: (user) => (/****/
                    <span>
                        <LinkButton onClick={() => this.showUpdateModal(user)}></LinkButton>
                        <LinkButton onClick={() => this.deleteUser(user.id)}></LinkButton>
                    </span>
                )
            },
        ];
    };

    deleteUser = (id) => {
        Modal.confirm({
            title: '?',
            okText: '',
            cancelText: '',
            onOk: () => {
                reqDeleteUser(id);
                this.getUsers();
            },
        });
    };
    /**/
    getUsers = async (pageNum) => {
        this.setState({loading: true});
        const result = await reqUsers(pageNum, this.state.pageSize, this.state.userName);
        const {list, total} = result.data;
        this.setState({total, users: list, loading: false});
    };
    /**/
    showUpdateModal = (user) => {
        this.user = user;
        this.setState({
            showStatus: 2
        })
    };
    /**/
    handleCancel = () => {
        if (this.updateForm) {
            this.updateForm.resetFields();
        }
        if (this.saveForm) {
            this.saveForm.resetFields();
        }
        this.setState({
            showStatus: 0
        })
    };
    /**/
    updateUser = () => {
        /**/
        this.updateForm.validateFields(async (error, values) => {
            if (!error) {
                this.setState({
                    showStatus: 0
                });
                const {realName, mobilePhone, email, companyName, description} = values;
                if (realName) {
                    this.user.realName = realName;
                }
                if (mobilePhone) {
                    this.user.mobilePhone = mobilePhone;
                }
                if (email) {
                    this.user.email = email;
                }
                if (companyName) {
                    this.user.companyName = companyName;
                }
                if (description) {
                    this.user.description = description;
                }
                this.updateForm.resetFields();/**/
                const result = await reqUpdateUser(this.user);
                if (result.code === 0) {
                    notification['success']({
                        message: '',
                    });
                }
                this.getUsers();
            }
        });
    };

    componentWillMount() {
        this.initColumns();
    }

    componentDidMount() {
        this.getUsers(1);
    }

    render() {
        const {users, loading, total, showStatus, userName, pageNum, pageSize} = this.state;
        const user = this.user;
        return (
            <div style={{padding: '20px 20px 0'}}>
                <div className='content-search'>
                    <Form layout="inline">
                        <Form.Item label=''>
                            <Input value={userName}
                                   onChange={event => this.setState({userName: event.target.value})}/>
                        </Form.Item>
                        <Form.Item>
                            <Button type='primary' onClick={() => {
                                this.getUsers(1);
                                this.setState({pageNum: 1})
                            }}></Button>
                        </Form.Item>
                    </Form>
                </div>
                <div className='add-button'/>
                <Table rowKey='id' dataSource={users} columns={this.columns} loading={loading}
                       pagination={{
                           defaultCurrent: 1,
                           current: pageNum,
                           defaultPageSize: pageSize,
                           total: total,
                           onChange: (value) => {
                               this.getUsers(value);
                               this.setState({pageNum: value})
                           }
                       }}/>
                <Modal
                    title=""
                    visible={showStatus === 2}
                    okText=''
                    cancelText=''
                    onOk={this.updateUser}
                    onCancel={this.handleCancel}>
                    <UpdateForm user={user} setForm={(form) => {
                        this.updateForm = form
                    }}/>
                </Modal>
            </div>

        )
    }
}
